import { axios } from '@/utils/request'

const api = {
    login: 'login',
    logout: 'logout',
    sendSms: '/account/sms',
    // get my info
    userInfo: '/user/info'
}
export default api

/**
 * login func
 * parameter: {
 *     username: '',
 *     password: '',
 *     remember_me: true,
 * }
 * @param parameter
 * @returns {*}
 */
export function login(parameter) {
    return axios({
        url: api.login,
        method: 'post',
        data: parameter
    })
}

export function logout() {
    return axios({
        url: api.logout,
        method: 'post',
        headers: {
            'Content-Type': 'application/json;charset=UTF-8'
        }
    })
}

