import notification from 'ant-design-vue/lib/notification'
function getExceptionInfo(err){
    if (((err.response || {}).data || {}).error && typeof (((err.response || {}).data || {}).error) == "object"){
        const errObj = err.response.data.error
        let errMsg = ""
        let count = 1
        for (let item in errObj){
            errMsg += count + "、" +errObj[item][0]+"。  "
            count ++
        }
        return errMsg || '请求出现错误，请稍后再试'
    }
    return ((err.response || {}).data || {}).error || ((err.response || {}).data || {}).message || '请求出现错误，请稍后再试'
}

function sendFailedNotification(err ,timeout){
    notification['warning']({
        message: this.getExceptionInfo(err),
        duration: timeout
    })
}

function sendSuccessNotification(msg,desc,timeout){
    notification['success']({
        message: msg,
        description: desc,
        duration: timeout
    })
}
export default { getExceptionInfo, sendFailedNotification, sendSuccessNotification }




