const getters = {
  device: state => state.app.device,
  theme: state => state.app.theme,
  color: state => state.app.color,
  showTabMenu: state => state.app.showTabMenu,
  token: state => state.user.token,
  avatar: state => state.user.avatar,
  nickname: state => state.user.name,
  welcome: state => state.user.welcome,
  roles: state => state.user.roles,
  userInfo: state => state.user.info,
  addRouters: state => state.permission.addRouters,


  // theme:state =>state.accounts.theme,
//客户资料功能的选中客户信息
  currentCustInfo: state => state.accounts.currentCustInfo,
//电销业务功能的选中客户信息
  ESPCustInfo: state => state.accounts.ESPCustInfo,
//电销业务功能的是否点击下一条的状态管理
  ESPCIisNext: state => state.accounts.ESPCIisNext,
//电销业务功能加载状态管理
  spBusiness: state => state.accounts.spBusiness,
//电销下的跟踪记录
  spTrackRecord: state => state.accounts.spTrackRecord,
//电销下的算价系统
  spCalculatePrice: state => state.accounts.spCalculatePrice,
  id: state => state.accounts.id,
  owner: state => state.accounts.owner,
  mobile: state => state.accounts.mobile,
  condition: state => state.accounts.condition,
  trackRecordCondition: state => state.accounts.trackRecordCondition,


//分发
  list: state => state.handout.list,
  //名单数
  listNum: state => state.handout.listNum,
  where: state => state.handout.where,
  cacheData: state => state.handout.cacheData,
  //每人分配数量
  perNum: state => state.handout.perNum,
  current: state => state.handout.current,
  handoutSuccess:state => state.handout.handoutSuccess,
  handoutResult:state => state.handout.handoutResult
}

export default getters


