<template>
  <a-breadcrumb class="breadcrumb">
    <a-breadcrumb-item
      v-for="(item, index) in breadList"
      :key="index"
    >
      <router-link
        v-if="item.name != name"
        :to="{ path: item.path }"
      >
        {{ item.meta.title }}
      </router-link>
      <span v-else>{{ item.meta.title }}</span>
    </a-breadcrumb-item>
  </a-breadcrumb>
</template>

<script>
import ABreadcrumb from 'ant-design-vue/lib/breadcrumb'
const ABreadcrumbItem = ABreadcrumb.Item
export default {
  components:{
    ABreadcrumb,
    ABreadcrumbItem
  },
  data() {
    return {
      name: "",
      breadList: []
    };
  },
  created() {
    this.getBreadcrumb();
  },
  methods: {
    getBreadcrumb() {
      this.breadList = [];
      this.breadList.push({
        name: "工作台",
        path: "/dashboard/workplace",
        meta: {
          title: "工作台"
        }
      });

      this.name = this.$route.name;
      this.$route.matched.forEach(item => {
        // item.meta.name === 'dashboard' ? item.path = '/dashboard' : this.$route.path === item.path
        this.breadList.push(item);
      });
    }
  },
  watch: {
    $route() {
      this.getBreadcrumb();
    }
  }
};
</script>

<style scoped>
</style>


