<template>
  <div class="user-wrapper">
    <span class="action">
      <a-icon type="question-circle-o"></a-icon>
    </span>
    <header-notice class="action"/>
    <a-dropdown>
      <span class="action ant-dropdown-link user-dropdown-menu">
        <!-- <a-avatar class="avatar" size="small" :src="avatar()"/> -->
        <a-avatar class="avatar" size="small" style="color: #fff;background: #ccc;" icon="user"/>
        <span>{{ nickname() }}</span>
      </span>
      <a-menu slot="overlay" class="user-dropdown-menu-wrapper">
        <a-menu-item key="0">
          <router-link :to="{ name: '个人中心' }">
            <a-icon type="user"/>
            <span>个人中心</span>
          </router-link>
        </a-menu-item>
        <a-menu-item key="1">
          <router-link :to="{ name: '个人设置' }">
            <a-icon type="setting"/>
            <span>个人设置</span>
          </router-link>
        </a-menu-item>
        <a-menu-item key="2" disabled>
          <a-icon type="setting"/>
          <span>测试</span>
        </a-menu-item>
        <a-menu-divider/>
        <a-menu-item key="3">
          <a href="javascript:;" @click="handleLogout">
            <a-icon type="logout"/>
            <span>退出登录</span>
          </a>
        </a-menu-item>
      </a-menu>
    </a-dropdown>
  </div>
</template>

<script>
  import HeaderNotice from './HeaderNotice'
  import AIcon from 'ant-design-vue/lib/icon'
  import ADropdown from 'ant-design-vue/lib/dropdown'
  import AAvatar from 'ant-design-vue/lib/avatar'
  import AMenu from 'ant-design-vue/lib/menu'
  const AMenuItem = AMenu.Item
  const AMenuDivider = AMenu.Divider
  import { mapActions, mapGetters } from 'vuex'

  export default {
    name: 'UserMenu',
    components: {
      HeaderNotice,
      AIcon,
      ADropdown,
      AAvatar,
      AMenu,
      AMenuItem,
      AMenuDivider
    },
    methods: {
      ...mapActions(['Logout']),
      ...mapGetters(['nickname', 'avatar']),
      handleLogout() {
        let _this = this
        this.$confirm({
          title: '提示',
          content: '真的要注销登录吗 ?',
          onOk() {
            return _this.Logout({}).then(() => {
              window.location.reload()
            }).catch(err => {
              _this.$myNotification.sendFailedNotification(err)
            })
            // sessionStorage.removeItem("user");
            // _this.$router.push("/login");
            // console.log("OK");
          },
          onCancel() {
          },
        })
      },
    }
  }
</script>

<style scoped>

</style>


