<template>
  <a-popover trigger="click" placement="bottomRight" :autoAdjustOverflow="true" :arrowPointAtCenter="true" overlayClassName="header-notice-wrapper" :overlayStyle="{ width: '300px', top: '50px' }">
    <template slot="content">
        <a-spin :spinning="loadding">
          <a-tabs>
            <a-tab-pane tab="通知" key="1">
              <a-list>
                <a-list-item>
                  <a-list-item-meta title="你收到了 14 份新周报" description="一年前">
                    <a-avatar style="background-color: white" slot="avatar" src="https://gw.alipayobjects.com/zos/rmsportal/ThXAXghbEsBCCSDihZxY.png"/>
                  </a-list-item-meta>
                </a-list-item>
                <a-list-item>
                  <a-list-item-meta title="你推荐的 曲妮妮 已通过第三轮面试" description="一年前">
                    <a-avatar style="background-color: white" slot="avatar" src="https://gw.alipayobjects.com/zos/rmsportal/OKJXDXrmkNshAMvwtvhu.png"/>
                  </a-list-item-meta>
                </a-list-item>
                <a-list-item>
                  <a-list-item-meta title="这种模板可以区分多种通知类型" description="一年前">
                    <a-avatar style="background-color: white" slot="avatar" src="https://gw.alipayobjects.com/zos/rmsportal/kISTdvpyTAhtGxpovNWd.png"/>
                  </a-list-item-meta>
                </a-list-item>
              </a-list>
            </a-tab-pane>
            <a-tab-pane tab="消息" key="2">
              123
            </a-tab-pane>
            <a-tab-pane tab="待办" key="3">
              123
            </a-tab-pane>
          </a-tabs>
        </a-spin>
</template>
    <span @click="fetchNotice" class="header-notice">
      <a-badge count="12">
        <a-icon style="font-size: 16px; padding: 4px" type="bell" />
      </a-badge>
    </span>
  </a-popover>
</template>

<script>
  import ASpin from 'ant-design-vue/lib/spin'
  import AIcon from 'ant-design-vue/lib/icon'
  import AList from 'ant-design-vue/lib/list'
  import ABadge from 'ant-design-vue/lib/badge'
  import ATabs from 'ant-design-vue/lib/tabs'
  import APopover from 'ant-design-vue/lib/popover'
  import AAvatar from 'ant-design-vue/lib/avatar'
  const AListItem = AList.Item
  const AListItemMeta = AListItem.Meta
  const ATabPane = ATabs.TabPane
  export default {
    name: 'HeaderNotice',
    components:{
      ASpin,
      AIcon,
      AList,
      AListItem,
      AListItemMeta,
      ABadge,
      ATabs,
      ATabPane,
      APopover,
      AAvatar
    },
    data() {
      return {
        loadding: false
      }
    },
    methods: {
      fetchNotice() {
        if (this.loadding) {
          this.loadding = false
          return
        }
        this.loadding = true
        setTimeout(() => {
          this.loadding = false
        }, 2000)
      }
    }
  }
</script>

<style lang="css">
  .header-notice-wrapper {
    top: 50px !important;
  }
</style>

<style lang="scss" scoped>
  .header-notice {
    display: inline-block;
    transition: all 0.3s;
    span {
      vertical-align: initial;
    }
  }
</style>


