import Mock from 'mockjs'
import { apiURL } from '@/apiSettings.js'
import { builder } from '../util'

const result =[]

const next = 6

const team = [
    '创展团队一',
    '创展团队二',
    '创展团队三',
]

const time = [
    '00:10:23',
    '00:06:03',
    '00:19:29',
    '00:28:49',
    '00:23:59',
    '00:10:52',
]

const policyNo = [
    'PDA02879890235428',
    'WD955324829234178',
    'P9232105828598712',
    'RKX14809802952395',
    'IMX85209080234234',
]

const plateNo = [
    '川AU9J66',
    '川AY2D33',
    '川A1JZ83',
    '川AECA3Q',
    '川A687SE',
]

const mobile = [
    '18266883042',
    '18819278234',
    '15268938197',
    '18273469841',
    '16683840608',
    '13523494755',
    '18289567212',
]

const performanceResult = (options) => {
    const result=[]
    for (let i = 1; i < next; i++) {
        const tmpKey = i
        result.push({
            key: tmpKey,
            id: tmpKey,
            policy_no: policyNo[i - 1],
            plate_no: plateNo[i - 1],
            mobile: mobile[i - 1],
            num_valid: Mock.mock('@integer(0, 10)'),
            talk_time: time[i - 1],
            num_calculate: Mock.mock('@integer(0, 5)'),
            team: team[Mock.mock('@integer(0, 2)')]
        })
    }
    return builder(result)
}




Mock.mock(apiURL + '/performanceresult', 'post', performanceResult);



