// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import 'babel-polyfill';//safari,ie等浏览器没有内置Promise对象，通过polyfill可解决
import Vue from 'vue'
import App from './App'
// import Button from 'ant-design-vue/lib/button'
import 'ant-design-vue/dist/antd.less'  // or 'ant-design-vue/dist/antd.less'
import '@/styles/style.css'
// import { Button,Menu,Icon } from 'ant-design-vue';
import router from './router/lazy'
import Viser from 'viser-vue'
import { VueAxios } from '@/utils/request'
import '@/utils/filter' // base filter
import localstorage from './localstorage'
import myNotification from '@/components/Tools/myNotification.js'
import Storage from 'vue-ls'
// import store from './store/index.js'
import store from './store/index.js'
import Api from './api/api.js'
import Axios from 'axios'
import trim from '@/components/Tools/trim'
import {notification, message} from 'ant-design-vue'
import Modal from 'ant-design-vue/lib/modal'
import NProgress from 'nprogress' // progress bar
import 'nprogress/nprogress.css' // progress bar style
import config from '@/defaultSettings'
import {
  //app
  ACCESS_TOKEN,
  DEFAULT_COLOR,
  DEFAULT_THEME,
  DEFAULT_LAYOUT_MODE,
  DEFAULT_COLOR_WEAK,
  SIDEBAR_TYPE,
  DEFAULT_FIXED_HEADER,
  DEFAULT_FIXED_HEADER_HIDDEN,
  DEFAULT_FIXED_SIDEMENU,
  DEFAULT_CONTENT_WIDTH_TYPE,
  DEFAULT_SHOW_TAB_MENU,
  //accounts
  CURRENT_CUST_INFO
} from '@/store/mutation-types'

import '@/mock'

// import Es6Promise from 'es6-promise'
// Es6Promise.polyfill()

Vue.prototype.$localstorage = localstorage
Vue.prototype.$myNotification = myNotification
Vue.prototype.$notification = notification
Vue.prototype.$message = message
Vue.prototype.$confirm = Modal.confirm

Vue.prototype.$trim=trim
Vue.prototype.$api=Api

Axios.defaults.baseURL = '/api'
Axios.defaults.headers.post['Content-Type'] = 'application/json';

Vue.config.productionTip = false

Vue.use(Storage, config.storageOptions)
Vue.use(VueAxios, router)
Vue.use(Viser)

// permission.js 通过import引入时，beforeEach钩子函数不生效，因此暂时放至main中

NProgress.configure({ showSpinner: false }) // NProgress Configuration

const whiteList = ['login', 'register', 'registerResult'] // no redirect whitelist

router.beforeEach((to, from, next) => {
  NProgress.start() // start progress bar
  if (Vue.ls.get(ACCESS_TOKEN)) {
    /* has token */
    if (to.path === '/login') {
      next({ path: '/dashboard/workplace' })
      NProgress.done()
    } else {
      const aa = store.getters.roles.length
      if (store.getters.roles.length === 0) {
        store
          .dispatch('GetInfo')
          .then(res => {
            const roles = res.result && res.result.role
            store.dispatch('GenerateRoutes', { roles }).then(() => {
              // 根据roles权限生成可访问的路由表
              // 动态添加可访问路由表
              router.addRoutes(store.getters.addRouters)
              const redirect = decodeURIComponent(from.query.redirect || to.path)
              if (to.path === redirect) {
                // hack方法 确保addRoutes已完成 ,set the replace: true so the navigation will not leave a history record
                next({ ...to, replace: true })
                // const bb = { ...to}
                // console.log(bb)
                // next(to.path)
              } else {
                // 跳转到目的路由
                next({ path: redirect })
              }
            })
          })
          .catch(() => {
            notification.error({
              message: '错误',
              description: '请求用户信息失败，请重试'
            })
            store.dispatch('Logout').then(() => {
              next({ path: '/login', query: { redirect: to.fullPath } })
            })
          })
      } else {
        next()
      }
    }
  } else {
    
    if (whiteList.includes(to.name)) {
      // 在免登录白名单，直接进入
      next()
    } else {
      next({ path: '/login', query: { redirect: to.fullPath } })
      NProgress.done() // if current page is login will not trigger afterEach hook, so manually handle it
    }
  }
})

router.afterEach(() => {
  NProgress.done() // finish progress bar
})


new Vue({
  // el: '#app',
  router,
  store,
  // components: { App },
  // template: '<App/>'
  // beforeCreate(){
  // },
  beforeCreate() {
    //app
    store.commit('SET_SIDEBAR_TYPE', Vue.ls.get(SIDEBAR_TYPE, true))
    store.commit('TOGGLE_THEME', Vue.ls.get(DEFAULT_THEME, config.navTheme))
    store.commit('TOGGLE_LAYOUT_MODE', Vue.ls.get(DEFAULT_LAYOUT_MODE, config.layout))
    store.commit('TOGGLE_FIXED_HEADER', Vue.ls.get(DEFAULT_FIXED_HEADER, config.fixedHeader))
    store.commit('TOGGLE_FIXED_SIDERBAR', Vue.ls.get(DEFAULT_FIXED_SIDEMENU, config.fixSiderbar))
    store.commit('TOGGLE_CONTENT_WIDTH', Vue.ls.get(DEFAULT_CONTENT_WIDTH_TYPE, config.contentWidth))
    store.commit('TOGGLE_FIXED_HEADER_HIDDEN', Vue.ls.get(DEFAULT_FIXED_HEADER_HIDDEN, config.autoHideHeader))
    store.commit('TOGGLE_WEAK', Vue.ls.get(DEFAULT_COLOR_WEAK, config.colorWeak))
    store.commit('TOGGLE_COLOR', Vue.ls.get(DEFAULT_COLOR, config.primaryColor))
    store.commit('TOGGLE_SHOW_TAB_MENU', Vue.ls.get(DEFAULT_SHOW_TAB_MENU, config.showTabMenu))
    store.commit('SET_TOKEN', Vue.ls.get(ACCESS_TOKEN))
    
    //accounts
    store.commit('CURRENT_CUST_INFO', Vue.ls.get(CURRENT_CUST_INFO))

    // removeLoadingAnimate('preloadingWrapper')
  },
  render: h => h(App)
}).$mount('#app')



